@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@700&display=swap");
@font-face {
  font-family: "Open Sauce One";
  src: url("/public/OpenSauceOne-Regular.ttf");
  font-weight: 400;
}
@font-face {
  font-family: "Open Sauce One";
  src: url("/public/OpenSauceOne-SemiBold.ttf");
  font-weight: 600;
}
@font-face {
  font-family: "Open Sauce One";
  src: url("/public/OpenSauceOne-Bold.ttf");
  font-weight: 700;
}
@font-face {
  font-family: "SF Pro Text";
  src: url("/public/FontsFree-Net-SFProText-Semibold.ttf");
  font-weight: 600;
}

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-sf-pro-text: "SF Pro Text";
  --font-open-sauce-one: "Open Sauce One";
  --font-manrope: Manrope;

  /* font sizes */
  --font-size-mid: 17px;
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --font-size-5xl: 24px;
  --font-size-lg: 18px;
  --font-size-25xl: 44px;
  --font-size-13xl: 32px;
  --font-size-71xl: 90px;
  --font-size-3xl: 22px;
  --font-size-17xl: 36px;
  --font-size-41xl: 60px;
  --font-size-29xl: 48px;

  /* Colors */
  --body: #0b1033;
  --color-gray-100: #020305;
  --white: #fff;
  --primary: #0045df;
  --neutral-ink: #67768e;
  --primary-blue: #274bac;
  --color-black: #000;

  /* Gaps */
  --gap-base: 16px;
  --gap-5xl: 24px;
  --gap-xs: 12px;
  --gap-5xs: 8px;
  --gap-9xs: 4px;
  --gap-sm: 14px;

  /* Paddings */
  --padding-xs: 12px;
  --padding-9xl: 28px;

  /* Border radiuses */
  --br-11xs: 2px;
  --br-9xs: 4px;
  --br-5xl: 24px;
  --br-base: 16px;
  --br-50xl: 69px;
  --br-12xs: 1px;
  --br-80xl: 99px;
}
