.dot {
  position: absolute;
  top: 1px;
  left: 1px;
  border-radius: var(--br-12xs);
  background-color: var(--neutral-ink);
  width: 4px;
  height: 4px;
}
.pattern1 {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.pattern1Group {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
